.navbar {
  background: transparent;
  padding: 0;
  border: 0;
  border-radius: 0;
  width: 100%;
}

.navbar .nav .nav-item {
  margin-right: 40px;
}

.navbar .nav .nav-item .nav-link {
  font: 500 14px/100px "Roboto", sans-serif;
  text-transform: uppercase;
  color: #05364d;
  padding: 0;
  font-size: 12px;
  display: inline-block;
}

.navbar .nav .nav-item .nav-link:after {
  display: none;
}

.navbar .nav .nav-item:hover .nav-link,
.navbar .nav .nav-item .nav-link.active {
  color: #efb6df;
}

.navbar .nav .nav-item.submenu {
  position: relative;
}

.navbar .nav .nav-item.submenu ul {
  border: none;
  padding: 0;
  border-radius: 0;
  box-shadow: none;
  margin: 0;
  background: #fff;
}

@media (min-width: 992px) {
  .navbar .nav .nav-item.submenu ul {
    position: absolute;
    top: 120%;
    left: 0;
    min-width: 200px;
    text-align: left;
    opacity: 0;
    transition: all 300ms ease-in;
    visibility: hidden;
    display: block;
    border: none;
    padding: 0;
    border-radius: 0;
    box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.1);
  }
}

.navbar .nav .nav-item.submenu ul:before {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #eee transparent transparent transparent;
  position: absolute;
  right: 24px;
  top: 45px;
  z-index: 3;
  opacity: 0;
  transition: all 400ms linear;
}

.navbar .nav .nav-item.submenu ul .nav-item {
  display: block;
  float: none;
  margin-right: 0;
  border-bottom: 1px solid #ededed;
  margin-left: 0;
  transition: all 0.4s linear;
}

.navbar .nav .nav-item.submenu ul .nav-item .nav-link {
  line-height: 45px;
  color: #05364d;
  padding: 0 30px;
  transition: all 150ms linear;
  display: block;
  text-transform: capitalize;
  margin-right: 0;
}

.navbar .nav .nav-item.submenu ul .nav-item:last-child {
  border-bottom: none;
}

.navbar .nav .nav-item.submenu ul .nav-item:hover .nav-link {
  color: #fff;
}

@media (min-width: 992px) {
  .navbar .nav .nav-item.submenu:hover ul {
    visibility: visible;
    opacity: 1;
    top: 100%;
  }
}

.navbar .nav .nav-item.submenu:hover ul .nav-item {
  margin-top: 0;
}

.navbar .nav .nav-item:last-child {
  margin-right: 0;
}

